<template>
  <div>
    <el-container class="home-container">
      <!-- 头部 -->
      <el-header>
        <div>
          <span></span>
        </div>
        <el-input
          placeholder="搜索歌曲"
          prefix-icon="el-icon-search"
          v-model="input"
          class="input"
          @keyup.enter.native="toResult"
        ></el-input>
      </el-header>
      <!-- 主体区域 -->
      <el-container>
        <el-aside width="200px">
          <el-menu default-active="1" :router="true" background-color="#ededed">
            <el-menu-item index="/discovery">
              <i class="el-icon-menu"></i>
              <span slot="title">发现音乐</span>
            </el-menu-item>
            <el-menu-item index="2" disabled>
              <i class="el-icon-document"></i>
              <span slot="title">推荐菜单</span>
            </el-menu-item>
            <el-menu-item index="3" disabled>
              <i class="el-icon-setting"></i>
              <span slot="title">最新音乐</span>
            </el-menu-item>
            <el-menu-item index="4" disabled>
              <i class="el-icon-setting"></i>
              <span slot="title">最新MV</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view ref="main"></router-view>
        </el-main>
      </el-container>
      <!-- 播放器 -->
      <div class="player">
        <audio
          @timeupdate="audioOntimeupdate"
          @seeked="audioOnseeked"
          :src="musicUrl"
          autoplay
          controls
          id="music"
        ></audio>
      </div>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: "", //搜索内容
      currentLine: 0, //当前播放到哪一句了
      currentTime: "", //当前播放时间
      ppxx: "" //歌词移动距离
    };
  },
  computed: {
    //获取歌曲播放url
    musicUrl: function() {
      return this.$store.state.musicUrl;
    },
    //获取歌词时间组
    lrcTime: function() {
      return this.$store.state.lrcTime;
    }
  },
  methods: {
    //查询跳转
    toResult() {
      if (this.input === "") {
        // 提示用户
        this.$message.warning("请输入内容!");
      } else {
        // 携带数据去搜索页面
        this.$router.push(`/result?q=${this.input}`);
      }
    },
    //音频当前的播放位置发送改变时触发
    audioOntimeupdate(e) {
      this.currentTime = e.target.currentTime;
      // var $lrclist = this.$refs.main.$children[0].$refs.lrclist;
      let $lrclist = this.$refs.main.$refs.lrclist;
      // var $li = $lrclist.getElementsByTagName("li"); //获取所有li
      for (let j = this.currentLine, len = this.lrcTime.length; j < len; j++) {
        if (
          this.currentTime < this.lrcTime[j + 1] &&
          this.currentTime > this.lrcTime[j]
        ) {
          this.currentLine = j;
          this.ppxx = -this.currentLine * 32;
          $lrclist.style.transform = "translateY(" + this.ppxx + "px)";
          // $li[this.currentLine].style.color = "black";
          // $li[this.currentLine + 1].style.color = "red";
          break;
        }
      }
    },
    //音频进度更改后触发
    audioOnseeked(e) {
      // var $lrclist = this.$refs.main.$refs.lrclist;
      // var $li = $lrclist.getElementsByTagName("li"); //获取所有li
      // $li[this.currentLine].style.color = "black";
      this.currentTime = e.target.currentTime;
      for (let k = 0, len = this.lrcTime.length; k < len; k++) {
        if (
          this.currentTime < this.lrcTime[k + 1] &&
          this.currentTime < this.lrcTime[k]
        ) {
          this.currentLine = k;
          break;
        }
      }
    }
  }
};
</script>

<style scoped lang="less">
.home-container {
  height: 100%;
}
.el-header {
  background-color: #f9f9f9;
  display: flex;
  // justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    margin-left: 15px;
    color: red;
    width: 200px;
  }
  > .el-input {
    width: 400px;
  }
}
.el-aside {
  background-color: #ededed;
  .el-menu {
    border-right: none;
  }
}

.player {
  background: #f1f3f4;
  height: 60px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

audio {
  width: 100%;
  border-radius: none;
  outline: none;
}
.el-main {
  // height: 700px;
  height: 100%;
  padding-bottom: 80px;
}
</style>
