import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

//解决vue-router 3.1.0 promise的问题
const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

const routes = [{
    path: "/",
    redirect: "/discovery"
  },
  {
    path: "/discovery",
    component: () => import( /* webpackChunkName: "about" */ "@/views/Discovery")
  },
  {
    path: "/result",
    component: () => import( /* webpackChunkName: "about" */ "@/views/Result")
  },
  {
    path: "/song",
    component: () => import( /* webpackChunkName: "about" */ "@/views/Song")
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

//挂载路由导航守卫
//如果没有query，查询歌曲和歌曲详情跳转到主页
router.beforeEach((to, from, next) => {
  if (to.path === '/result' && (to.query.q == '' || to.query.q == undefined)) {
    return next('/discovery')
  }
  if (to.path === '/song' && to.query.id == '') {
    return next('/discovery')
  }
  next()
})

export default router;