import Vue from "vue";
import Vuex from "vuex";

//1.安装插件
Vue.use(Vuex);

//2.创建并导出Vuex.Store对象
export default new Vuex.Store({
  state: {
    //播放器的url
    musicUrl: "",
    //歌词时间数组
    lrcTime: ""
  },
  mutations: {
    changeUrl(state, url) {
      state.musicUrl = url
    },
    changeLrcTime(state, url) {
      state.lrcTime = url
    },
  }
});